}

.debug {
  .debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
  display: none;
}

body, html {
  background: #EDEEF0;
  height: 100%;
}

body, html {
  display: flex;
  flex-direction: column;
  font: 16px/1.625 'Open Sans', Arial, Helvetica, Verdana, sans-serif;
  color: #000;
  text-align: left;

  p {
    color: #333333;
    margin-top: 7px;
    margin-bottom: 7px;
  }

  strong {
    font-weight: bold;
    color: @color_strong;
  }

  em {
    font-style: italic;
  }
}

.wrapper {
  // border: 1px solid green;
  // box-sizing: border-box;
}

nav {
  /*Fix menu start*/
  top: 0px;
  position: fixed;
  width: 100%;
  z-index: 1000;
  /*end*/
  background: @color_main;

  height: 45px;

  .wrapper {
    position: relative;
    background: @color_main;

  }

  .clearfix {
    content: "";
    display: table;
    clear: both;
  }


  .menu {
    margin: 0 auto;

    &__li {
      float: left;
      padding-left: 30px;
      padding-right: 30px;

      a {
        display: inline-block;
        margin-top: 10px;
        margin-bottom: 8px;
        text-decoration: none;
        font-size: 15px;
        color: #fff;
        text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);

        @media screen and  (min-width: 721px) {

          &:hover {
            border-bottom: 3px solid white;
          }

          &.active {
            border-bottom: 3px solid white;
          }
        }
      }
    }
  }

  .menuToggle {
    padding: 10px 30px;
    font-size: 15px;
    color: #fff;
    text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
    display: none;
    cursor: pointer;
  }

  @media screen and  (max-width: 720px) {
    .menu {
      position: absolute;
      background: @color_main;
      opacity: 90%;
      display: none;

      &__li {
        float: none;
      }
    }

    .menuToggle {
      display: inline-block;
    }
  }

}

#main {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
  //border: 1px solid red;
  .wrapper {
    margin-top: 65px;
  }
}

.main {
  //border: 1px solid red;
  .row-flex();
  flex-wrap: nowrap;
  align-items: flex-start;

  /* &__left{
      .col();
      .size(3);
      min-width: 278px;
      font: 16px/1.5 Arial, Verdana, sans-serif;
  } */


  &__right {
    .col();
    .size(9);
