/* Left sideBar Start*/
.main {
  &__left {
    display: flex;
    flex-direction: column;

    &__title {
      align-self: center;
      color: #472047;
      text-shadow: -5px 5px 5px rgba(0, 0, 0, 0.3);
      font-weight: 700;
      font-size: 15px;
      margin-top: 5px;
      margin-bottom: 5px;

      p {
        color: @color_title;

        .link {
          text-decoration: none;
          color: @color_title;
        }

        .link:hover {
          text-decoration: underline;
        }
      }

    }

    &__widget {
      //border: 1px solid rgba(80, 114, 153, 0.5);
      color: #494F53;
      //border-radius: 3px;
      background: #fff;

      &__form {
        width: 100%;

        &__mail {
          display: flex;
          flex-direction: column;
          align-items: center;
          width: 100%;
          border-bottom: 1px dashed #8c8c8c;

          &__text {
            width: 250px;
            height: 30px;
            background: #EDEEF0;
            border: none;
            box-shadow: -1px -1px 1px #aaaaad;
            color: #afafaf;
            margin-top: 15px;
            font: italic normal 12px Arial, sans-serif;
            color: #666665;
            padding-left: 7px;

          }
        }
      }

      &__forPicture {
        color: #4175b3;
        font-size: 12px;
        padding: 7px 12px;
      }

      &__cat {
        font-size: 12px;
        padding: 7px 12px;
        line-height: 1.6;

        &__li a {
          text-decoration: none;
          color: #4175b3;
        }
      }
    }
  }
}

/* Left sideBar End*/

/* Main page post design -- start*/
.main {

  &__right {
    header {
      .title {
        text-align: center;
        margin-bottom: 15px;

        &__description {
          font-size: 22px;
          font-weight: 900;
          color: @color_title;
          text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
        }
      }
    }

    &_post_bar_date {
      font-size: 12px;

      span {
        color: #000;
      }
    }

    .prevBord {
      border: 1px solid #999;
      padding: 2px;

      .prev2 {
        border: 4px solid #eee;
        padding: 5px;

        &__title {
          text-align: center;

          a h2 {

            display: inline-block;
            text-decoration: none;
            font-size: 22px;
            color: #616161;
            font-weight: 700;
            cursor: pointer;
          }
        }

        &__description {
          font-size: 16px;
          color: @color_desc;
        }

        &__img {
          width: 100%;
        }

        &__next {
          display: block;
          width: 150px;
          padding-top: 4px;
          background: #e1eef7;
          font-size: 14px;
          text-align: center;
          color: #0033CC;
          cursor: pointer;
          margin-top: 5px;
          margin-bottom: 5px;
        }
      }
    }

    &__section {
      border: 1px solid #507299;
      border-radius: 10px;
      background: #fff;
      color: #333;
      margin-top: 15px;

      h2 {
        display: block;
        color: #333333;
        font-size: 16px;
        font-weight: 700;
        padding: 7px;
        cursor: pointer;
        border: 1px solid #507299;
        border-radius: 10px;
        background: #f3f3f3;
        color: #333;
        text-shadow: -5px 5px 5px rgba(0, 0, 0, 0.3);

      }

      &__pict {
        width: 100%;
      }

      &__slide {
        display: none;
        margin-left: 10px;
        margin-right: 10px;

        &__title {
          text-align: center;
          margin-top: 5px;
          margin-bottom: 5px;

          a h3 {
            display: inline-block;
            text-decoration: none;
            font-size: 22px;
            color: #616161;
            font-weight: 700;
            cursor: pointer;
          }
        }

        p {
          color: @color_desc;
        }

        &__desc {
          color: @color_desc;
        }

        &__quote {
          background: #f3f3f3;
          border: 2px solid #e6e6e6;
          border-left: 2px solid #3965a8;
          margin-top: 15px;

          p {
            margin: 15px;
            color: #000;

            span {
              font-weight: 700;;
              color: @color_desc;
            }
          }
        }

        &__next {
          display: block;
          width: 150px;
          padding-top: 4px;
          background: #e1eef7;
          font-size: 14px;
          text-align: center;
          color: #0033CC;
          cursor: pointer;
          margin-top: 5px;
          margin-bottom: 10px;
        }

        &__continue {
          font-weight: 700;;
          color: @color_desc;
          text-align: center;
          margin-top: 15px;
          margin-bottom: 15px;
        }
      }
    }

    &__appael {
      background: #f3f3f3;
      border: 2px solid #E6E6E6;
      border-left: 2px solid #3965a8;
      margin-top: 15px;
      margin-bottom: 15px;
      overflow: hidden;
      width: 100%;

      p {
        margin: 15px;
      }
    }
  }
}

.relative {
  position: relative;

  &__data {
    position: absolute;
    top: 45px;
    left: 0px;
    background-color: white;
    padding: 1px;
  }
}

